.features {
  padding: 198px 0 96px 227px;
  background-image: url(./../images/features/Bg_2.jpg);
  background-size: cover;

  &__info {
    width: 400px;

    &_title {
      margin-bottom: 21px;

      &_text {
        margin-left: 16px;
        font-weight: 800;
        letter-spacing: 2px;
        text-transform: uppercase;
        vertical-align: top;
        color: $text-color-white;
      }
    }

    &_heading {
      margin-bottom: 69px;
    }

    &_block {
      display: flex;
      height: 96px;
      align-items: center;
      margin-bottom: 64px;

      &_image {
        width: 64px;
        height: 64px;
        border-radius: 32px;
        margin-right: 32px;
      }

      &_text {
        color: white;
        text-align: justify;
      }
    }
  }
}

@media screen and (max-width: 1280px) {
  .features {
    padding: 50px 20px;
    background-color: $background-color-blue;

    &__info {
      width: auto;

      &_heading {

        &_title {
          text-align: center;
        }
      }
    }
  }
}

@media screen and (max-width: 1024px) {
  .features {
    padding: 100px;

    &__info {
      width: 400px;
    }
  }
}

@media screen and (max-width: 768px) {
  .features {
    padding: 50px;

    &__info {
      width: 400px;
    }
  }
}

@media screen and (max-width: 425px) {
  .features {
    &__info {
      width: auto;

      &_block {
        height: auto;
      }
    }
  }
}
