.slider {
  position: relative;

  &__image {
    width: 1146px;
    height: 640px;
  }

  &__intro {
    position: absolute;
    left: 98px;
    bottom: 86px;

    &_heading {
      color: $background-color-blue;
      text-transform: uppercase;
      font-weight: 800;
      font-size: 12.6px;
      letter-spacing: 2px;
      margin-bottom: 4px;
    }

    &_text {
      font-weight: 900;
      font-size: 19.6px;
      line-height: 1.4;
    }
  }

  &__button {
    position: absolute;
    left: -80px;
    top: calc(50% - 31px);
    display: flex;
    width: 160px;

    &_left {
      @include sliderButton(url(./../images/slider/Vector_left.svg));
      border-top-left-radius: 64px;
      border-bottom-left-radius: 64px;
    }

    &_right {
      @include sliderButton(url(./../images/slider/Vector_right.svg));
      border-top-right-radius: 64px;
      border-bottom-right-radius: 64px;
    }
  }
}

.carousel {
  list-style: none;
  padding-inline-start: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  display: flex;
  transform: translateX();
}

.container {
  width: 1146px;
  height: 640px;
  overflow: hidden;
  position: relative;
}

@media screen and (max-width: 1366px) {
  .slider {
    &__image {
      width: 917px;
      height: 533px;
    }
  }

  .container {
    width: 917px;
    height: 533px;
  }
}

@media screen and (max-width: 1024px) {
  .slider {

    &__image {
      width: 700px;
      height: 400px;
    }
  }

  .container {
    width: 700px;
    height: 400px;
  }
}

@media screen and (max-width: 768px) {
  .slider {
    &__image {
      width: 600px;
      height: 400px;
    }

    &__intro {
      left: 60px;
      bottom: 60px;
    }

    &__button {
      top: calc(100% - 32px);
      left: calc(50% - 80px);
    }
  }

  .container {
    width: 600px;
    height: 400px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 570px) {
  .slider {
    &__image {
      width: 360px;
      height: 201px;
    }

    &__intro {
      left: 30px;
      bottom: 30px;

      &_heading {
        font-size: 10px;
      }

      &_text {
        font-size: 13px;
      }
    }

    &__button {
      width: 80px;
      top: calc(100% - 16px);
      left: calc(50% - 40px);

      &_left {
        @include sliderButton(url(./../images/slider/Vector_left.svg));
        width: 40px;
        height: 32px;
      }

      &_right {
        @include sliderButton(url(./../images/slider/Vector_right.svg));
        width: 40px;
        height: 32px;
      }
    }
  }

  .container {
    width: 360px;
    height: 201px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media screen and (max-width: 425px) {
  .slider {
    &__image {
      width: 280px;
      height: 176px;
    }

    &__intro {
      left: 20px;
      bottom: 20px;

      &_heading {
        font-size: 10px;
      }

      &_text {
        font-size: 13px;
      }
    }

    &__button {
      width: 80px;
      top: calc(100% - 16px);
      left: calc(50% - 40px);

      &_left {
        @include sliderButton(url(./../images/slider/Vector_left.svg));
        width: 40px;
        height: 32px;
      }

      &_right {
        @include sliderButton(url(./../images/slider/Vector_right.svg));
        width: 40px;
        height: 32px;
      }
    }
  }

  .container {
    width: 280px;
    height: 168px;
    margin-left: auto;
    margin-right: auto;
  }
}
